	module.exports = {"data":{"site":{"siteMetadata":{"title":"weiran.co","author":"Weiran Zhang"}},"markdownRemark":{"id":"/Users/weiran/git/weiran/weiran.co/src/pages/blog/2013/4/google-wants-to-control-new-dotless-search-domain/index.md absPath of file >>> MarkdownRemark","html":"<blockquote>\n<p>The most interesting plan here is to use .search to operate a redirect service on the “on the ‘dotless’ .search domain (<a href=\"http://search/\">http://search/</a>) that, combined with a simple technical standard, will allow a consistent query interface across firms that provide search functionality, and will enable users to easily conduct searches with firms that provide the search functionality that they designate as their preference.”</p>\n</blockquote>\n<p>So the biggest search engine would control the new ‘search’ domain, other search engines would have to beg them for access, and they would be privvy to every search performed through that top level domain.</p>\n<p>Sounds great.</p>","frontmatter":{"title":"Google wants to control new 'dotless' search domain","date":"April 11, 2013","passthroughUrl":"http://techcrunch.com/2013/04/10/google-wants-to-operate-search-as-a-dotless-domain/"}}},"pathContext":{"slug":"/blog/2013/4/google-wants-to-control-new-dotless-search-domain/","previous":{"fields":{"slug":"/blog/2013/4/apple-didnt-ban-comic-but-got-the-blame-anyway/"},"frontmatter":{"title":"Apple didn't ban comic but got the blame anyway","passthroughUrl":"http://blog.comixology.com/2013/04/10/ceo-on-saga-12-controversy/","type":null}},"next":{"fields":{"slug":"/blog/2013/4/samsung-galaxy-simpler-than-iphone-according-to-samsungs-ad-agency/"},"frontmatter":{"title":"Samsung Galaxy simpler than iPhone according to Samsung's ad agency","passthroughUrl":"http://news.cnet.com/8301-17852_3-57578754-71/samsung-galaxy-simpler-than-iphone-survey-says/","type":null}}}}

